body {
  padding: 50px;
  background-color: #ffe1e1;
  font-family: sans-serif;
}

#container {
  
  display: -ms-flexbox;
  
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column-reverse;
      flex-direction: column-reverse;
}

.todoListMain .header input {
  padding: 10px;
  font-size: 16px;
  border: 2px solid #FFF;
  width: 165px;
}

.todoListMain .header button {
  padding: 10px;
  font-size: 16px;
  margin: 10px;
  margin-right: 0px;
  background-color: #f9b7b7;
  color: #FFF;
  border: 2px solid #ffd1d1;
}

.todoListMain .header button:hover {
  background-color: #ea8888;
  border: 2px solid #ea8888;
  cursor: pointer;
}

.todoListMain .theList {
  list-style: none;
  color: red;
  padding-left: 0;
  width: 250px;
}

.todoListMain .theList li {
  color: #333;
  background-color: rgba(255, 255, 255, .5);
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 5px;
  list-style: none;
}

ul.theList {
  padding: 0;
  list-style: none;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column-reverse;
      flex-direction: column-reverse;
}


.theList li {
  color: #333;
  background-color: rgba(255, 255, 255, .5);
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 5px;
  -webkit-transition: background-color .2s ease-out;
  -o-transition: background-color .2s ease-out;
  transition: background-color .2s ease-out;
}

.todoListMain .theList li:hover {
  background-color: pink;
  cursor: pointer;
}

 ul.theList li:hover{
	background-color: red;
	color: #ffffff;
	cursor: pointer;
}


